import React from 'react'
import { isNone, none, Option, some } from 'fp-ts/lib/Option'
import { useSelector } from 'react-redux'

import * as Store from '~/Store'
import { Language } from '~/Data'

type Markup = { __html: string }

type Translation = {
    copied: string
    paginator: {
        previous: string
        of: string
        next: string
    }
    subscription: {
        heading: string
        subheading: Markup
        form: {
            firstName: {
                label: string
                required: string
            }
            email: {
                label: string
                required: string
                invalid: string
            }
        }
        submit: string
        subscribed: string
        resubscribe: string
    }
    suggestions: {
        heading: string
    }
}

const fa: Translation = {
    copied: ' ',
    paginator: {
        previous: '',
        of: '',
        next: '',
    },
    subscription: {
        heading: '',
        subheading: {
            __html: '               <b>      </b>.',
        },
        form: {
            firstName: {
                label: ' ',
                required: '     ',
            },
            email: {
                label: '',
                required: '    ',
                invalid: '   ',
            },
        },
        submit: ' ',
        subscribed: '   .',
        resubscribe: ' ',
    },
    suggestions: {
        heading: ' ',
    },
}

type Translations = { [K in Language]: Translation }

const translations: Translations = { [Language.FA]: fa }

const context = React.createContext<Option<Translation>>(none)

export function provider(props: React.PropsWithChildren<{}>) {
    const language = useSelector(Store.getLanguage)

    const { Provider } = context

    return <Provider {...props} value={some(translations[language])} />
}

export function ask(): Translation {
    const translation = React.useContext(context)

    if (isNone(translation)) {
        throw new Error(`
      No Translation provided.
      Please ensure you have provided Handlers.
    `)
    }

    return translation.value
}
